import React from 'react';
import HamburgerMenu from '../components/HamburgerMenu'; 

const CaseStudyTemplate = ({ pageContext: { study } }) => {
  return (
    <div style={{ padding: '20px', maxWidth: '800px', margin: '0 auto' }}>
      <HamburgerMenu /> 
      <h1>{study.name}</h1>
      <p><strong>Location:</strong> {study.location}, {study.country}</p>
      <p><strong>Coordinates:</strong> {study.lat}, {study.lng}</p>
      <p><strong>Short Description:</strong> {study.shortDescription}</p>
      <p><strong>Description:</strong> {study.description}</p>
      <p><strong>Total Area:</strong> {study.totalArea_km2} kmÂ²</p>
      <p><strong>System Digital Twinned:</strong> {study.systemDigitalTwinned}</p>
      <p><strong>Start Year:</strong> {study.startYear}</p>
      <p><strong>End Year:</strong> {study.endYear}</p>
      <p><strong>Creators:</strong> {study.creators}</p>
      <p><strong>Clients/Sponsors:</strong> {study.clientsSponsors}</p>
      <p><strong>Users:</strong> {study.users}</p>
      <p><strong>Status:</strong> {study.status}</p>
      <p><strong>3D Platform:</strong> {study["3DPlatform"]}</p>
      <p><strong>3D Platform Features:</strong> {study["3DPlatformFeatures"]}</p>
      <p><strong>3D Platform to Physical City Control:</strong> {study["3DPlatformToPhysicalCityControl"]}</p>
      <p><strong>Decision Making:</strong> {study.decisionMaking}</p>
      <p><strong>Contested Claims/Challenges:</strong> {study.contestedClaimsChallenges}</p>
      <img src={study.imagePath} alt={study.name} style={{ maxWidth: '100%', borderRadius: '8px', marginTop: '20px' }} />
    </div>
  );
};

export default CaseStudyTemplate;
